@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300&display=swap');
.App
{
  margin: 0;
  box-sizing: border-box;
  background: #333333;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #dd1818, #333333);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #dd1818, #333333); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  min-height: 100vh;
  font-family: 'Ubuntu';
}
.food
{
  display:flex;
  flex-direction: row;
  justify-content:space-evenly;
  flex-wrap:wrap;
 
}

.card
{
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  min-width: 40%;
  height: 380px;
  padding-top: 80px;
  padding-bottom: 80px;
  margin-bottom: 10px;
  margin-top: 35px;
  margin-right: 20px;
  margin-left: 20px;
  box-shadow: 0 0 10px 5px rgb(150, 121, 68);
  border-radius: 2px;
  transform: perspective(700px) rotateY(var(--rotate-y,0))  translateY(var(--translate-y,0));
  transform-style:preserve-3d;
  transition: 750ms;
  cursor: pointer;
  background: #080f1a;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #4e312c, #21262c);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #994f44, #c27e75); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  border-radius: 5px;
  color: rgb(32, 31, 31);

  
}

.card.flip
{
  --rotate-y : 180deg;
}
.card:hover
{
  box-shadow: 0 0 10px 5px rgb(223, 113, 22);
  --translate-y: -4px;
  

}
.card .front,
.card .back
{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  backface-visibility: hidden;
  padding:25px;
  
  
}

.card .back
{
  transform: rotateY(180deg);
}
.front .image
{
 
  border-radius: 20px;
}
.title
{
  display: flex;
  justify-content: center;
  font-family: 'Ubuntu';
  color: #DDD6F3;
  margin-left: 20px;
  font-size: 35px;
  text-shadow: 2px 2px 8px rgb(230, 225, 225);
}
.desc
{
  display: flex;
  justify-content: center;
  color: #DDD6F3;
  margin-left: 20px;
  font-size: 20px;

  
}
.search-form
{
  display: flex;
  justify-content:center ;
  align-items: center;
  width: 100%;
}
.search-bar
{
  width:40%;
  height: 40px;
  justify-content: center;
  margin-top: 20px;
  background-color: rgb(250, 248, 245);
  border: none;
  font-family: 'Gochi Hand';
  font-size: 20px;
  outline: none;
}
.search-button
{
  
  width:5%;
  cursor: pointer;
  height: 42px;
  background-color: rgb(250, 248, 245);
  margin-top: 20px;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
  
 
}
.search-button:hover
{
  background-color: rgb(226, 202, 166);
}



@media (max-width:1430px) {
  .card{

    min-width: 40%;
      
  }

  .desc
  {
    margin-right: 10px;
    margin-left: 10px;
  }
}
@media (max-width:500px){
  .card{

    min-width: 80%;
  }

  .desc, .title
  {
    margin-right: 6px;
    margin-left: 6px;
  }
  .search-bar
  {
    width: 65%;
  }
  .search-button
  {
    width: 20%;
  }
}
@media (max-width:400px){
  .card{

    min-width: 85%;

  }
  .title
  {
    margin-right: 10px;
    margin-left: 10px;
    font-size: 30px;
  }
  .desc
  {
    margin-right: 10px;
    margin-left: 10px;
  }
  .search-bar
  {
    width: 65%;
  }
  .search-button
  {
    width: 20%;
  }
}
@media (max-width:350px){
  .card{

    min-width: 80%;
    

  }
  .v
  .title
  {
    margin-right: 10px;
    margin-left: 10px;
    font-size: 25px;
  }
  .desc
  {
    margin-right: 10px;
    margin-left: 10px;
  }
  .search-bar
  {
    width: 65%;
  }
  .search-button
  {
    width: 20%;
  }
}